<template>
  <div class="wrap-all-command">
    <div v-for="(all, a ) in allCommandestwo" :key="`${all.id}-${a}`">
      <div v-if="all.status == !1">
        <div class="card grey">
          <div class="card_image">
            <h4 class="title-top-command">{{all.id}}</h4>
            <div class="items-text-command">
              <p class="title-product">{{all.title}}</p>
              <div class="contaier-btn">
                <button class="items-button" @click="changeStatus(all.id,all.status,all)">Valider</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "./../../../axios-auth";

export default {
  data() {
    return {
      totalcommand: [],
      allCommandestwo: []
    };
  },
  created() {
    this.allCommande();
  },
  methods: {
    allCommande() {
      axios
        .get(`/orders/getOrdersAll`)
        .then(res => {
          this.totalcommand = this.totalcommand.concat(res.data);
          let o = [];
          let p = [];
          this.totalcommand.forEach(i => {
            var id = i.id;
            if (!o[id]) {
              return p.push((o[id] = i));
            }
            return (o[id].title = o[id].title + " + " + i.title); //\\
          });
          this.allCommandestwo = p;
          this.statusProduct();
        })
        .catch(err => {
          console.log(err);
        });
    },
    changeStatus(id, status, all) {
      axios
        .put(`/orders/ordersStatus/${id}`)
        .then(res => {
          console.log(res.data);
          all.status = 1;
          this.statusProduct();
          this.totalcommand = [];
          this.allCommandestwo = [];
          this.allCommande();
        })
        .catch(err => {
          console.log(err);
        });
    },
    statusProduct() {
      let product = [];
      this.allCommandestwo.forEach(item => {
        if (item.status === 0) {
          return product.push(item);
        }
      });
      this.allCommandestwo = product;
    }
  }
};
</script>
