<template>
  <div id="signin">
    <div class="signin-form" :class="{ active: isActive}">
      <h2 class="text-center">Se connecter</h2>
      <p class="icon">
        <i class="la la-cutlery"></i>
      </p>
      <form @submit.prevent="onSubmit">
        <div class="input">
          <label for="email" >Mail</label>
          <input name="email" type="email" v-validate="'required|email'" id="email" v-model="email"  />
           <span class="error" v-show="errors.has('email')">{{ errors.first('email')}}</span>
        </div>
        <div class="input">
          <label for="password">Password</label>
          <input name="mots de passe login" type="password" v-validate="'required'" id="password" v-model="password" />
          <span class="error" v-show="errors.has('mots de passe login')">{{ errors.first('mots de passe login')}}</span>
        </div>
        <div class="submit">
          <button type="submit">Se connecter</button>
        </div>
      </form>
      <p>Vous n'avez pas de compte ?</p>
      <p class="input-signup" @click="ActiveClass()">Créer un compte</p>
    </div>
    <div class="signup-form" :class="{ active: !isActive}">
            <h2 class="text-center">Créer un compte</h2>
      <p class="icon">
        <i class="la la-cutlery"></i>
      </p>
      <form @submit.prevent="onSubmitSignup">
        <div class="input">
          <label for="last_name">Nom</label>
          <input name="name" v-validate="'required|alpha_spaces'" type="text" id="last_name" v-model="last_name" />
          <span class="error" v-show="errors.has('name')">{{ errors.first('name')}}</span>
        </div>
        <div class="input">
          <label for="first_name">Prenom</label>
          <input name="prenom" type="text" v-validate="'required'"  id="first_name" v-model="first_name" />
          <span class="error" v-show="errors.has('prenom')">{{ errors.first('prenom')}}</span>
        </div>
        <div class="input">
          <label for="email">Mail</label>
          <input name="mail" type="email" id="email-" v-validate="'required|email'"   v-model="email" />
          <span class="error" v-show="errors.has('mail')">{{ errors.first('mail')}}</span>
        </div>
        <div class="input">
          <label for="password">Password</label>
          <input name="password" type="password" id="password-" v-validate="'required|minimum|number|upCase'"  v-model="password" />
          <span class="error" v-show="errors.has('password')">{{ errors.first('password')}}</span>


        </div>
        <div class="input">
          <label for="confirm-password">Confirmer le password</label>
          <input name="confirmation du password" type="password" id="confirm-password-" v-validate="'required'" v-model="confirmPassword" />
          <span class="error" v-show="errors.has('confirmation du password')">{{ errors.first('confirmation du password')}}</span>
        </div>
        <div class="submit">
          <button type="submit">S'inscrire</button>
        </div>
      </form>
      <p class="input-signup" @click="ActiveClass()">Vous avez un compte ? Se connecter</p>

    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      email: "",
      password: "",
      last_name: "",
      first_name: "",
      confirmPassword: "",
      isActive: true,
    };
  },
  created(){
      this.$validator.extend('upCase', {
      getMessage: () => "Manque une majuscule",
      validate: value => value.match(/[A-Z]/g) !== null
    })
    this.$validator.extend('number', {
      getMessage: () => "Un chiffre",
      validate: value => value.match(/[0-9]/g) !== null
    });
    this.$validator.extend('minimum', {
      getMessage: () => "Le mot de passe doit contenir au moins 6 caractères ",
      validate: value => value.match(/^.{6,}$/g) !== null
    })

  },
  methods: {
    onSubmit() {
      const formData = {
        email: this.email,
        password: this.password
      };
      // console.log(formData);
      this.$store
        .dispatch("login", {
          email: formData.email,
          password: formData.password
        })
        .then(res => {
          this.$router.push("/dashboard");
        })
        .catch(error => {
          if (error) {
            let err = "Mots de passe incorret";
            alert(err);
            console.log(err);
          }
        });
    },
          onSubmitSignup () {
        const formData = {
          last_name : this.last_name,
          first_name : this.first_name,
          email: this.email,
          password: this.password,
          //confirmPassword: this.confirmPassword,
        }
        // console.log(formData)
        this.$store.dispatch('signup', formData).then(
          res => {
            if(res){
              console.log(res)
              alert(`c'est un succès`)
            }else{
              alert(`Le mots de passe doit contenir au moins 8 caractères,une majuscule et un chiffre `)
            }
          }
        ).catch(err=>{
          alert(err)
        })
        this.isActive = !this.isActive

      },
      ActiveClass(){
        this.isActive = !this.isActive
      }

  }
};
</script>

