<template>
    <div class="wrap-produits-total">
      <table class="table">
        <tr>
          <th>Id</th>
          <th>title</th>
          <th>Description</th>
          <th>Status</th>
        </tr>
        <tr
          v-for="(command, c) in visibleTodosCommand"
          v-bind:visibleTodosCommand="visibleTodosCommand"
          v-bind:key="`${command.id}-${c}`"
        >
          <!-- <td>{{product.id}}</td> -->
          <td>{{command.id}}</td>
          <td>{{command.title}}</td>
          <td>{{command.description}}</td>
          <td>{{command.status}}</td>
        </tr>
      </table>
      <!-- Pagination debut -->
      <div class="pagination-wrap" v-if="totalePagesCommand() > 0">
        <span v-if="showPreviousLinkCommand()" class="pagination-btn" @click="updatePageCommand(currentPageCommand - 1)">
          <i class="la la-angle-left"></i>
        </span>
        {{currentPageCommand + 1}} sur {{totalePagesCommand()}}
        <span
          v-if="showNextLinkCommand()"
          class="pagination-btn"
        >
          <i class="la la-angle-right" @click="updatePageCommand(currentPageCommand + 1)"></i>
        </span>
      </div>
      <!-- Fin de la pagination -->
    </div>

</template>

<script>
import axios from "./../../../axios-auth";
export default {
    data(){
        return{
      //Commande
      commande : [],
      currentPageCommand: 0,
      pageSizeCommand: 5,
      visibleTodosCommand: []
        }
    },
    created(){
        this.viewAllCommande()
    },
    methods : {
        updatePageCommand(pageNumber) {
      this.currentPageCommand = pageNumber;
      this.updateVisibleTodosCommand();
    },

    updateVisibleTodosCommand() {
      this.visibleTodosCommand = this.commande.slice(
        this.currentPageCommand * this.pageSizeCommand,
        this.currentPageCommand * this.pageSizeCommand + this.pageSizeCommand
      );
      // console.log(this.visibleTodosCommand)
      if (this.visibleTodosCommand.length === 0 && this.currentPageCommand > 0) {
        this.updatePageCommand(this.currentPageCommand - 1);
      }
      this.totalePagesCommand();
    },
    totalePagesCommand() {
      // console.log(this.pageSize)
      var totale = Math.ceil(this.commande.length / this.pageSizeCommand);
      return totale;
    },
    showPreviousLinkCommand() {
      return this.currentPageCommand  == 0 ? false : true;
    },
    showNextLinkCommand() {
      return this.currentPageCommand  == this.totalePagesCommand() - 1 ? false : true;
    },
        viewAllCommande() {
      axios
        .get("/orders/getOrders")
        .then(res => {
          console.log(res.data);
          this.commande = this.commande.concat(res.data);
          this.updateVisibleTodosCommand();
        })
        .catch(err => {
          console.log(err);
        });
    }
    }
}
</script>
