<template>
  <div>
    <div class="submit-add">
      <span @click="removeFormAdd()">
        <i class="la la-plus-circle submit"></i>
      </span>
    </div>
    <div class="wrap-produits-total">
      <table class="table">
        <tr>
          <!-- <th>Id</th> -->
          <th>Name</th>
          <th>Description</th>
          <th>Prix</th>
          <th>Status</th>
          <th>Cathegorie</th>
          <th>Modifiez</th>
        </tr>
        <tr
          v-for="product in visibleTodos"
          v-bind:visibleTodos="visibleTodos"
          v-bind:key="product.id"
        >
          <!-- <td>{{product.id}}</td> -->
          <td>{{product.title}}</td>
          <td>{{product.description}}</td>
          <td>{{product.price}}</td>
          <td>{{product.status}}</td>
          <td>{{product.category}}</td>
          <td>
            <i @click="editProduct(product)" class="la la-pencil btn"></i>
          </td>
        </tr>
      </table>
      <!-- Pagination debut -->
      <div class="pagination-wrap" v-if="totalePages() > 0">
        <span v-if="showPreviousLink()" class="pagination-btn" @click="updatePage(currentPage - 1)">
          <i class="la la-angle-left"></i>
        </span>
        {{currentPage + 1}} sur {{totalePages()}}
        <span
          v-if="showNextLink()"
          class="pagination-btn"
        >
          <i class="la la-angle-right" @click="updatePage(currentPage + 1)"></i>
        </span>
      </div>
      <!-- Fin de la pagination -->
    </div>
    <div>
      <div class="card-form" :class="{'is-form': isForm}">
        <i class="la la-close btn-close-form" @click="removeFormEdit()"></i>
        <h2 class="title">Modifier le produit</h2>
        <form @submit.prevent="onSubmit" :class="{'is-text': isText}">
          <div class="input">
            <label for="title">Nom du plats</label>
            <input type="text" id="title" v-model="produit.title" required />
          </div>
          <div class="input">
            <label for="description">Description</label>
            <input type="text" id="description" v-model="produit.description" />
          </div>
          <div class="input">
            <label for="price">Le prix</label>
            <input type="number" step="0.01" id="price" v-model="produit.price" />
          </div>
          <div class="input">
            <label for="category">Cathegorie</label>
            <select id="category" v-model="produit.category" required>
              <option>--Please choose an option--</option>
              <option >Entrée</option>
              <option>Plats</option>
              <option>Dessert</option>
            </select>
          </div>
          <div class="input">
            <label for="status">Disponible ?</label>
            <input type="checkbox" id="status" v-model="produit.status" />
          </div>
          <div class="submit">
            <button type="submit">Modifier</button>
            <!-- <button type="submit">Edit</button> -->
          </div>
        </form>
        <form @submit.prevent="onSubmitImg" :class="{'is-img': isImg}">
          <div class="input">
            <label for="file">Image</label>
            <input type="file" id="file" @change="onFileChange($event)" />
          </div>
          <div class="submit">
            <button type="submit">Modifier</button>
          </div>
        </form>
        <div :class="{'btn-active': isBtnActive}">
          <button @click="ActiveEditText()" class="submit">Modifier le texte</button>
          <button @click="ActiveEditImg()" class="submit">Modifier l'image</button>
        </div>
      </div>
    </div>
    <div>
      <div class="card-form2" :class="{'is-form-add': isFormAdd}">
        <i class="la la-close btn-close-form" @click="removeFormAdd()"></i>
        <h2 class="title">Ajouter un produit</h2>
        <form @submit.prevent="onSubmitAdd">
          <div class="input">
            <label for="file">Image</label>
            <input type="file" id="file" @change="onFileChange($event)" />
          </div>
          <div class="input">
            <label for="title">Nom du plats</label>
            <input type="text" id="title" v-model="produit.title" required />
          </div>
          <div class="input">
            <label for="description">Description</label>
            <input type="text" id="description" v-model="produit.description" />
          </div>
          <div class="input">
            <label for="category">Cathegorie</label>
            <select id="category" v-model="produit.category" required>
              <option>--Please choose an option--</option>
              <option >Entrée</option>
              <option>Plats</option>
              <option>Dessert</option>
            </select>
          </div>
          <div class="input">
            <label for="price">Le prix</label>
            <input type="number" step="0.01" id="price" v-model="produit.price" />
          </div>
          <div class="input">
            <label for="status">Disponible ?</label>
            <input type="checkbox" id="status" v-model="produit.status" />
          </div>
          <div class="submit">
            <button type="submit">Ajouter</button>
            <!-- <button type="submit">Edit</button> -->
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "./../../../axios-auth";
export default {
  data() {
    return {
      products: [],
      produit: {
        id: null,
        title: "",
        description: "",
        price: "",
        source: "",
        category: "",
        price: "",
        status: true
      },
      // pic: "",
      // Form modif
      isForm: false,
      isText: true,
      isImg: true,
      isBtnActive: false,
      // Form d'ajout
      isFormAdd: false,
      // Start pagination
      currentPage: 0,
      pageSize: 4,
      visibleTodos: []
    };
  },
  created() {
    this.viewProduct();
  },
  beforeMount: function() {
    this.updateVisibleTodos();
  },
  methods: {
    //Debut de pagination
    updatePage(pageNumber) {
      this.currentPage = pageNumber;
      this.updateVisibleTodos();
    },

    updateVisibleTodos() {
      this.visibleTodos = this.products.slice(
        this.currentPage * this.pageSize,
        this.currentPage * this.pageSize + this.pageSize
      );
      // console.log(this.visibleTodos)
      if (this.visibleTodos.length === 0 && this.currentPage > 0) {
        this.updatePage(this.currentPage - 1);
      }
      this.totalePages();
    },
    totalePages() {
      // console.log(this.pageSize)
      var totale = Math.ceil(this.products.length / this.pageSize);
      return totale;
    },
    showPreviousLink() {
      return this.currentPage == 0 ? false : true;
    },
    showNextLink() {
      return this.currentPage == this.totalePages() - 1 ? false : true;
    },

    //---Fin de Pagination---
    editProduct(product) {
      // console.log(product);
      this.produit = product;
      // console.log(this.produit);
      this.removeFormEdit();
    },
    removeFormEdit() {
      this.isForm = !this.isForm;
      this.isBtnActive = false;
      this.isText = true;
      this.isImg = true;
    },
      // Modification de l'image du produit
    onFileChange(event) {
      this.produit.source = event.target.files[0];
      // console.log(this.produit.source)
    },
    onSubmitImg() {
      // console.log(this.produit.source);
      let form = new FormData();
      form.append("productImage", this.produit.source);
      // console.log(form);
      axios
        .put(`/product/putProductImg/${this.produit.id}`, form)
        .then(res => {
          // console.log(res.data);
          this.isForm = !this.isForm;
          // console.log(this.isForm);
        })
        .catch(err => {
          console.log(err);
        });
    },
    /** Fin */
    //Modification du text du produit
    onSubmit() {
      // console.log(this.produit.source);
      if (this.produit.status == true) {
        this.produit.status = 1;
      } else {
        this.produit.status = 0;
      }
      let data = {
        title: this.produit.title,
        description: this.produit.description,
        price: this.produit.price,
        category: this.produit.category,
        status: this.produit.status
      };
      axios
        .put(`/product/putProduct/${this.produit.id}`, data)
        .then(res => {
          // console.log(res.data);
          this.isForm = !this.isForm;
          // console.log(this.isForm);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // Ajout de produit
    onSubmitAdd() {
      // console.log("toto", this.produit);
      if (this.produit.status == true) {
        this.produit.status = 1;
      } else {
        this.produit.status = 0;
      }
      let form = new FormData();
      form.append("productImage", this.produit.source);
      form.append("title", this.produit.title);
      form.append("description", this.produit.description);
      form.append("price", this.produit.price);
      form.append("status", this.produit.status);
      form.append("category", this.produit.category);
      // console.log(this.produit.category)
      axios
        .post(`/product/addProduct`, form)
        .then(res => {
          // console.log(res.data);
          this.isFormAdd = !this.isFormAdd;
        })
        .catch(err => {
          console.log(err);
        });
    },
    removeFormAdd() {
      this.isFormAdd = !this.isFormAdd;
      // console.log(this.visibleTodos);
    },
    // fin de l'ajout
    ActiveEditText() {
      this.isText = !this.isText;
      this.isBtnActive = !this.isBtnActive;
    },
    ActiveEditImg() {
      this.isImg = !this.isImg;
      this.isBtnActive = !this.isBtnActive;
    },
    // Récupere tout les produits
    viewProduct() {
      axios
        .get("/product/getProduct")
        .then(res => {
          // console.log(res.data);
          this.products = this.products.concat(res.data);
          this.updateVisibleTodos();
        })
        .catch(err => {
          console.log(err);
        });
    }
    // fin de la recuperation de tout les produits
  }
};
</script>
