<template>
  <div>
    <div>
      <div>
        <!-- <button>
          <i class="la la-cart-arrow-down"></i>
          <span>{{badge}}</span>
        </button>-->
        <div id="cart" :class="{'is-active': isActive}">
          <div class="header-panier">
            <h2 class="title-panier">Votre panier</h2>

            <button @click="removeCarts()" class="btn-btn close-cart">
              <i class="la la-close close-panier"></i>
            </button>
          </div>
          <div data-aos="zoom-in-down"  v-for="(cart,n) in carts" v-bind:key="`${n}-cart.id`" class="middle">
            <img
              :src="cart.source"
              :alt='cart.name'
              class="item-img"
            />
            <h3 class="item-name">{{cart.title}}</h3>
            <span class="item-price">{{cart.price}}</span>
            <button @click="removeCart(n)" class="btn-btn">
              <i class="la la-close close-panier"></i>
            </button>
          </div>
          <div v-if="this.carts <= 0">
            <p>Votre pannier est vide</p>
          </div>
          <div class="bottom-panier">
            Total price : {{totalprice}}
            <button @click="addOrder()" class="btn-btn chekckout">Valider</button>
          </div>
        </div>
      </div>
    </div>
    <div>
      <span class="line">
        <h2>
          <span>Entrée</span>
        </h2>
      </span>
      <div class="listofproduct">
        <div data-aos="zoom-in-down" v-for="product in entrée" v-bind:key="product.id" class="product">
          <img class="iteme-img" :src="product.source" :alt='product.name' />
          <h2>{{product.title}}</h2>
          <!-- <p>{{product.description}}</p> -->
          <div class="price-add">
            <p>{{product.price}}</p>
            <i @click="addCart(product)" class="la la-cart-arrow-down cart-add"></i>
          </div>
        </div>
      </div>
      <span class="line">
        <h2>
          <span>Plats</span>
        </h2>
      </span>
      <div class="listofproduct">
        <div data-aos="zoom-in-down"  v-for="product in plats" v-bind:key="product.id" class="product">
          <img class="iteme-img" :src="product.source" :alt="product.name" />
          <h2>{{product.title}}</h2>
          <!-- <p>{{product.description}}</p> -->
          <div class="price-add">
            <p>{{product.price}}</p>
            <i @click="addCart(product)" class="la la-cart-arrow-down cart-add"></i>
          </div>
        </div>
      </div>
      <span class="line">
        <h2>
          <span>Dessert</span>
        </h2>
      </span>
      <div class="listofproduct">
        <div data-aos="zoom-in-down"  v-for="product in dessert" v-bind:key="product.id" class="product">
          <img class="iteme-img" :src="product.source" :alt="product.name" />
          <h2>{{product.title}}</h2>
          <!-- <p>{{product.description}}</p> -->
          <div class="price-add">
            <p>{{product.price}}</p>
            <i @click="addCart(product)" class="la la-cart-arrow-down cart-add"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "./../../axios-auth";
import { EventBus } from "../../event-bus";
export default {
  data() {
    return {
      isActive: false,
      products: [],
      entrée: [],
      plats: [],
      dessert: [],
      product: {
        id: "",
        title: "",
        source: "",
        description: "",
        price: ""
      },
      add: true,
      edit: false,
      carts: [],
      cartadd: {
        id: "",
        title: "",
        description: "",
        price: ""
      },
      badge: "0",
      totalprice: "0"
    };
  },
  created() {
    EventBus.$on("toggle-cart", status => {
      // console.log("status");
      //console.log(status);
      this.isActive = status;
    });
    this.viewProduct();
    this.viewCart();

  },
  methods: {
    viewCart() {
      if (localStorage.getItem("carts")) {
        this.carts = JSON.parse(localStorage.getItem("carts"));
        this.badge = this.carts.length;
        this.totalprice = this.carts.reduce((total, item) => {
          return total + item.price;
        }, 0);
      }
    },
    addCart(produit) {
      this.cartadd.id = produit.id;
      this.cartadd.title = produit.title;
      this.cartadd.description = produit.description;
      this.cartadd.source = produit.source;
      this.cartadd.price = produit.price;
      this.carts.push(this.cartadd);
      this.cartadd = {};
      this.storeCart();
    },
    removeCart(produit) {
      console.log(produit);
      this.carts.splice(produit, 1);
      this.storeCart();
    },
    storeCart() {
      let parsed = JSON.stringify(this.carts);
      localStorage.setItem("carts", parsed);
      this.viewCart();
    },
    viewProduct() {
      axios
        .get("/product/getProduct")
        .then(res => {
          //console.log(res)
          // console.log(res.data)
          // localStorage.setItem('carts',JSON.stringify(res.data))
          this.products = this.products.concat(res.data);
          //console.log(this.products)
          this.products.forEach(element => {
            if (element.category === "Entrée" && element.status == 1 ) {
              this.entrée.push(element);
            } else if (element.category === "Plats" && element.status == 1 ) {
              this.plats.push(element);
            } else if(element.category === "Dessert" && element.status == 1  ) {
              this.dessert.push(element);
            }
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    removeCarts() {
      this.isActive = !this.isActive;
    },
    addOrder(){
      console.log(this.carts)
      let formData = {
        products_id : []
      }
      for(let i = 0; i < this.carts.length; i++){
        console.log(this.carts[i].id)
        formData.products_id.push(this.carts[i].id)
      }
      console.log(formData)
      axios.post(`/orders/orders`,formData).then(res =>{
        console.log(res.data)
        this.carts = null
        localStorage.removeItem('carts')
        this.totalprice = 0
      })

    },
  }
};
</script>
