<template>
 <div>
     <div class="cards-list">
      <div data-aos="fade-right"  class="card blue">
        <div class="card_image">
          <h4 class="title-top">Nombre de commande</h4>
          <div class="items-text">
          <p>{{this.allcommand}}</p>
          <p><i class="la la-shopping-cart"></i></p>

          </div>
        </div>
      </div>

      <div data-aos="fade-right" class="card blue ">

        <div class="card_image">
          <h4 class="title-top">Nombre d'user</h4>
          <div class="items-text">
          <p>{{this.allUsers}}</p>
          <p><i class="la la-users"></i></p>

          </div>
        </div>

      </div>

      <div data-aos="fade-right"  class="card blue">
        <div class="card_image">
          <h4 class="title-top">Facebook</h4>
          <div class="items-text" >
          <p>20</p>
          <p><i class="la la-facebook"></i></p>

          </div>
        </div>

      </div>

      <div data-aos="fade-right" class="card blue">
        <div class="card_image">
          <h4 class="title-top">Revenue</h4>
          <div>
            <div class="items-text" >
          <p>{{totalPrice}}</p>
         <p> <i class="la la-money"></i></p>
          </div>
          </div>
        </div>
      </div>
    </div>
 </div>
</template>
<script>
import axios from "./../../../axios-auth";
export default {
    data() {
    return {
      allcommand: null,
      allUsers: null,
      totalPrice :  null,

    };
    },
    created(){
      this.allCommandLength();
      this.getTotalUsers();

    },
    methods:{
      allCommandLength(){
              axios
        .get(`/orders/getOrdersAll`)
        .then(res=>{
            // console.log(res.data)
            this.allcommand = res.data.length
            let total = 0
            for(let i = 0; i < res.data.length; i++){
               total = total + res.data[i].price
            }
            this.totalPrice = total
        })
        .catch(err=>{
          return err
        })
      },
      getTotalUsers(){
          axios.get(`/user/allusers`)
          .then(res=>{
              this.allUsers = res.data.data.length
          })
          .catch(err=>{
            return err
          })
      },
    }
}
</script>
