<template>
<div class="btn-wrap">
    <button class="btn-primary" @click="deleteAccount()">Supprimer votre compte</button>
</div>

</template>
<script>
import axios from "./../../../axios-auth";

export default {

    methods:{
        deleteAccount(){
            axios.delete(`/user/deleteUser`).then(res=>{
                 this.$store.dispatch('destroyToken').then(res => { // Pour detruire le token
                 this.$router.push('/signin')
      })
            }).catch(err=>console.log(err))
        }
    }

}
</script>
